	text-align: center;
	color: #2c3e50;
	margin-top: 60px;
}

.login-container {
	border-radius: 15px;
	background-clip: padding-box;
	margin: 180px auto;
	width: 350px;
	padding: 35px 35px 15px 35px;
	background: #fff;
	border: 1px solid #eaeaea;
	-webkit-box-shadow: 0 0 25px #cac6c6;
	        box-shadow: 0 0 25px #cac6c6;
}
.login_title {
	margin: 0 auto 40px auto;
	text-align: center;
	color: #505458;
}
.login_remember {
	margin: 0 0 35px 0;
	text-align: left;
}

.home_container {
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}
.el-header {
	background-color: #24292E;
	color: #333;
	text-align: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.el-aside {
	background-color: #ECECEC;
}
.el-main {
	background-color: #fff;
	color: #000;
	text-align: center;
}
.home_title {
	color: #fff;
	font-size: 22px;
	display: inline;
}
.avatar {
	border-radius: 50%;
	width: 22px;
	vertical-align: middle;
}
.home_userinfo {
	color: #fff;
	cursor: pointer;
}
.home_userinfoContainer {
	display: inline;
	margin-right: 20px;
}

