.el-tag {
    background-color: #e9eaea;
    border-color: #d3d4d5;
    color: #24292e;
    display: inline-block;
    height: 32px;
    padding: 0 10px;
    line-height: 30px;
    font-size: 12px;
    color: #24292E;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
